
<template>
    <div class="container">
        <div class="main__cars" v-if="getUsers">
            <button @click="add">НАЖМИТЕ КНОПКУ</button>
            <div class="main__cars-item" v-for="item in getUsers" :key="item.id">
                <p>{{ item.id }}</p>
                <p>{{ item.title }}</p>
                <h2>{{ item.completed }}</h2>
            </div>
        </div>
        
        <div v-else class="Loading" >
             WAITING.......
        </div>
    </div>
</template>

<script>
    export default {
        computed: {
            getUsers() {
                return this.$store.state.users
            },
        },
        methods: {
            add() {
            this.$store.dispatch('addUser')
            }
        },
        
    }
</script>




